      if (this.imageList.length === 0) {
        uni.showToast({title: 'ËØ∑‰∏ä‰º†‰∏ªÈ°µÊà™Âõæ', icon: 'none'});
        return false;
      }
      if (!this.formData.activitySource) {
        uni.showToast({title: 'ËØ∑ÈÄâÊã©ÊÇ®ÊòØÂ¶Ç‰ΩïÂæóÁü•Êú¨Ê¨°Ê¥ªÂä®ÁöÑ', icon: 'none'});
        return false;
      }
      if (this.formData.rulesConfirmed !== 1) {
        uni.showToast({title: 'ËØ∑ÈòÖËØªÂπ∂ÂêåÊÑè„Ää‰ΩèÂ∞èÂèÆËØïÁù°ÂÆòÊåáÂçó„Äã', icon: 'none'});
        return false;
