    },

    // ‰∏ä‰º†ÂõæÁâá
    async uploadImages() {
      try {
        console.log('ÂºÄÂßã‰∏ä‰º†ÂõæÁâáÔºåÂÖ±', this.imageList.length, 'Âº†');

        // uploadFiles ËøîÂõûÁöÑÊòØÂØπË±°Êï∞ÁªÑ [{name, url}, {name, url}, ...]
        const results = await uploadFiles(this.imageList);
        console.log('‰∏ä‰º†ÊàêÂäüÔºåÁªìÊûú:', results);

        // ÊèêÂèñ url ÁªÑÊàêÊï∞ÁªÑ
        const urls = results.map(item => item.url);
        console.log('ÊèêÂèñÁöÑ URLs:', urls);

        // JSON Â≠óÁ¨¶‰∏≤ÂåñÂ≠òÂÇ®
        return JSON.stringify(urls);
      } catch (error) {
        console.error('‰∏ä‰º†ÂõæÁâáÂ§±Ë¥•:', error);
        // Êèê‰æõÊõ¥ËØ¶ÁªÜÁöÑÈîôËØØ‰ø°ÊÅØ
        let errorMsg = 'ÂõæÁâá‰∏ä‰º†Â§±Ë¥•';
        if (error.message) {
          if (error.message.includes('Áä∂ÊÄÅÁ†Å')) {
            errorMsg = 'ÊúçÂä°Âô®ÂìçÂ∫îÂºÇÂ∏∏ÔºåËØ∑Á®çÂêéÈáçËØï';
          } else if (error.message.includes('ÁΩëÁªú')) {
            errorMsg = 'ÁΩëÁªúËøûÊé•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªú';
          } else {
            errorMsg = error.message;
          }
        }
        throw new Error(errorMsg);
      }
    },

    // ‰øùÂ≠òË°®ÂçïÊï∞ÊçÆÂà∞ÁºìÂ≠ò
    saveFormData() {
      const formCache = {
        formData: this.formData,
        imageList: this.imageList,
        timestamp: Date.now()
      };
      uni.setStorageSync('questionnaire_form_cache', formCache);
    },

    // ÊÅ¢Â§çË°®ÂçïÊï∞ÊçÆ
    restoreFormData() {
      try {
        const formCache = uni.getStorageSync('questionnaire_form_cache');
        if (formCache) {
          // Ê£ÄÊü•ÁºìÂ≠òÊòØÂê¶ËøáÊúüÔºà24Â∞èÊó∂Ôºâ
          const isExpired = Date.now() - formCache.timestamp > 24 * 60 * 60 * 1000;
          if (!isExpired) {
            // ÊÅ¢Â§çË°®ÂçïÊï∞ÊçÆ
            this.formData = {...this.formData, ...formCache.formData};
            this.imageList = formCache.imageList || [];
          } else {
            // ÁºìÂ≠òËøáÊúüÔºåÊ∏ÖÈô§
            this.clearFormCache();
          }
        }
      } catch (error) {
        console.error('ÊÅ¢Â§çË°®ÂçïÊï∞ÊçÆÂ§±Ë¥•:', error);
      }
    },

    // Ê∏ÖÈô§Ë°®ÂçïÁºìÂ≠ò
    clearFormCache() {
      uni.removeStorageSync('questionnaire_form_cache');
      uni.removeStorageSync('sleep_tester_agreement_confirmed');
    },

    // Êèê‰∫§Ë°®Âçï
    async handleSubmit() {
      if (!this.validateForm()) {
        return;
      }

      // ‰øùÂ≠òÂΩìÂâçË°®ÂçïÊï∞ÊçÆ
      this.saveFormData();

      // ‰ΩøÁî®Áªü‰∏ÄÁöÑÁôªÂΩïÂ§ÑÁêÜÔºå‰º†ÈÄíÊ≥®ÂÜåÊ∏†ÈÅì
      await handleLoginRequired({
        url: '/pages/subPackages/questionnaire/detail',
        params: {},
        registerChannel: 'ËØïÁù°ÂÆòÊ¥ªÂä®',
        hotelId: null,
        success: async () => {
          await this.doSubmit();
        }
      });
    },

    // ÊâßË°åÂÆûÈôÖÁöÑÊèê‰∫§Êìç‰Ωú
    async doSubmit() {
      if (this.submitting) {
        return;
      }

      this.submitting = true;

      try {
        // ÊòæÁ§∫Âä†ËΩΩÊèêÁ§∫
        uni.showLoading({
          title: 'Êèê‰∫§‰∏≠...',
          mask: true
        });
        // ‰∏ä‰º†ÂõæÁâá
        this.formData.screenshotUrl = await this.uploadImages();
        // Â§ÑÁêÜÂπ≥Âè∞Êï∞ÊçÆ
        const submitData = {
          ...this.formData,
          platforms: this.formData.platforms.join(',')
        };
        // Êèê‰∫§Ë°®Âçï
        const res = await submitQuestionnaire(submitData);
        uni.hideLoading();

        // Êèê‰∫§ÊàêÂäüÔºåÊ∏ÖÈô§ÁºìÂ≠òÁöÑË°®ÂçïÊï∞ÊçÆÂíåÂçèËÆÆÁ°ÆËÆ§Ê†áËÆ∞
        this.clearFormCache();
        uni.removeStorageSync('sleep_tester_agreement_confirmed');

        // ÊòæÁ§∫‰ºÅ‰∏öÂæÆ‰ø°‰∫åÁª¥Á†ÅÂºπÁ™ó
        this.showQrcodeModal = true;
      } catch (error) {
        uni.hideLoading();
        console.error('Êèê‰∫§Â§±Ë¥•', error);
        uni.showToast({
          title: error.message || 'Êèê‰∫§Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      } finally {
        this.submitting = false;
      }
    }
  }
};
</script>

